.tabsContainer {
    margin-bottom: 160px;
}

.commandsContainer {
    position: absolute !important;
    bottom: 50px;
    left: 0;
    min-width: 135px;
    min-height: 44px;
    background: #282828;
    border: 1px solid #212121;
    display: flex;
    z-index: 997;
}

.commandsHeader {
    display: flex;
}

.commandsTitle {
    margin: 10px 5px 5px 5px;
    color: dimgrey;
}

.commands {
    margin: 10px;
    color: white;
}

.time {
    font-size: 0.9rem;
    color: #9e9e9e;
    letter-spacing: 1px;
    margin-right: 5px;
}

.hostname {
    margin-right: 5px;
}

.command {
}

.exitStatus {
    margin-left: 5px;
    color: #F44336;
}

.controlsMenuLeft {
    height: 100%;
    border-right: 1px solid #212121;
}

.controlsMenuLeft button:hover {
    background: transparent !important;
}

.controlsMenuLeft button {
    margin: 5px;
    display: block;
}

.controlsMenuRight {
    height: 100%;
}

.controlsMenuRight button {
    margin: 5px 10px 5px 5px;
    display: block;
}

.controlsMenuRight button:hover {
    background: transparent !important;
}

@media only screen and (max-height: 488px) {
    .commandsContainer {
        visibility: hidden;
    }
}

.showCommandsButton {
    position: absolute;
    bottom: 42px;
    right: 0;
}

.toggleCommandsButton {
    position: absolute;
    right: 15px;
}

.toggleCommandsButton:hover {
    background: transparent !important;
}

.shrink {
    animation: shrink .25s forwards;
}

.expand {
    animation: expand .25s forwards;
}

@keyframes shrink {
    0% {
        left: 0;
        width: 100vw
    }
    100% {
        left: 200px;
        width: calc(100vw - 200px)
    }
}

@keyframes expand {
    0% {
        left: 200px;
        width: calc(100vw - 200px)
    }
    100% {
        left: 0;
        width: 100vw
    }
}


