.container {
    padding: 0 20px;
}

.breadcrumb {
    font-size: 25px;
    padding: 0 5px;
    word-break: break-all;
    display: inline;
    opacity: 0.25;
}

.breadcrumb:last-child {
    opacity: 1;
}

.breadcrumb:before {
    font-size: 20px;
    margin: 4px 10px 0 0;
    color: #616161;
}
